/*eslint linebreak-style: ["error", "windows"]*/

window.onload = function () {
    const burgerBtn = document.querySelector(".burger-menu");
    const burgerNav = document.querySelector(".burger-nav");
    const icon = document.querySelector(".header-icon");
    const headerButton = document.querySelector(".header-btn");
    const modalWindow = document.querySelector(".modal-window");
    const modalClose = document.querySelector(".btn-close");
    const burgerNavButton = document.querySelector(".burger-nav__button");




    burgerBtn.addEventListener("click", () => {
        burgerNav.classList.toggle("active");


        // проверяем есть ли класс активности для того чтобы убрать скролл
        if (burgerNav.classList.contains("active")) {
            hideScroll();
        } else {
            showScroll();
        }
    });


    icon.addEventListener("click", () => {
        window.scrollTo(pageYOffset, 0);
    });


    // modal
    headerButton.addEventListener("click", () => {

        document.body.style.paddingRight = `${getScrollbarWidth()}px`;
        document.body.style.overflow = "hidden";
        modalWindow.classList.add("modal-window__active");
        // проверяем есть ли класс активности для того чтобы убрать скролл
        // if (modalWindow.classList.contains("modal-window__active")) {
        //     hideScroll();
        // } else {
        //     showScroll();
        // }
    });


    burgerNavButton.addEventListener("click", (event) => {
        event.preventDefault();
        modalWindow.classList.add("modal-window__active");
        // document.body.style.overflow = "hidden";
    });

    modalClose.addEventListener("click", (event) => {
        event.preventDefault();
        modalWindow.classList.remove("modal-window__active");
        document.body.style.paddingRight = "";
        document.body.style.overflow = "visible";

    });

};





// убирает бургер если ресайзить страницу

const resetNav = () => {
    const burgerNav = document.querySelector(".burger-nav");
    burgerNav.classList.remove("active");
    showScroll();
};

window.addEventListener("resize", resetNav);

// const header = document.querySelector(".section-header");
const mainNavigation = document.querySelector(".header");






// функции которые прячут скролл и возвращают когда включен бургер
const hideScroll = () => {
    const scrollWidth = `${getScrollbarWidth()}px`;

    document.body.style.paddingRight = scrollWidth;
    document.body.style.overflow = "hidden";

    mainNavigation.style.paddingRight = scrollWidth;
};

const showScroll = () => {

    document.body.style.paddingRight = "";
    document.body.style.overflow = "visible";


    mainNavigation.style.paddingRight = "";

};



// get scrollbar width
const getScrollbarWidth = () => {
    const outer = document.createElement("div");

    outer.style.position = "absolute";
    outer.style.top = "-9999px";
    outer.style.width = "50px";
    outer.style.height = "50px";
    outer.style.overflow = "scroll";
    outer.style.visibility = "hidden";


    document.body.appendChild(outer);
    const scrollBarWidth = outer.offsetWidth - outer.clientWidth;
    document.body.removeChild(outer);

    return scrollBarWidth;
};